@use "global" as *;

.p-mv {
  background-image: url(../images/common/mv-bg-sp.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center 30%;
  width: 100%;
  height: 100%;
  margin-top: rem(60);

  @include mq(md) {
    margin-top: rem(120);
    background-image: url(../images/common/mv-bg.jpg);
    background-position: center center;
  }
}

.p-mv__contents {
  padding: rem(40) 0 rem(400);
  text-align: center;

  @include mq(md) {
    text-align: left;
    padding: rem(224) 0 rem(272) rem(141);
  }
}

.p-mv__title {
  font-size: rem(22);

  @include mq(md) {
    font-size: rem(30);
  }
}

.p-mv__sub-title {
  margin-top: rem(12);
  font-size: rem(14);
  line-height: calc(21/14);

  @include mq(md) {
    margin-top: rem(32);
  }
}